import {Navigate, Route, Routes, Outlet} from 'react-router-dom'
import {PageLink, PageTitle} from '../../../_metronic/layout/core'
import {OrderManager} from './components/OrderManager'
import {ManagerPost} from './components/ManagerPost'
import {Policy} from './components/Policy'
import {ExportReport} from './components/ExportReport'
import {RatingAndReport} from './components/RatingAndReport'
import {Dao} from '../dao/Dao'
import {Baocaovipham} from '../dao/Baocaovipham'
import {FeedDetail} from './components/ListComponents/FeedDetail/FeedDetail'
import {FeedEdit} from './components/ListComponents/FeedEdit/FeedEdit'
import {OrderEdit} from './components/ListComponents/OrderEdit/OrderEdit'
import {OrderDetail} from './components/ListComponents/OrderDetail/OrderDetail'
import {RatingReportDetail} from './components/ListComponents/RatingReportDetail/RatingReportDetail'
const widgetsBreadCrumbs: Array<PageLink> = [
  {
    title: 'Widgets',
    path: '/admin-manager/charts',
    isSeparator: false,
    isActive: false,
  },
  {
    title: '',
    path: '',
    isSeparator: true,
    isActive: false,
  },
]

const AdminManage = () => {
  return (
    <Routes>
      <Route element={<Outlet />}>
        {/*  Start Order */}
        <Route
          path='managerorder'
          element={
            <>
              <PageTitle breadcrumbs={widgetsBreadCrumbs}>Charts</PageTitle>
              <OrderManager />
            </>
          }
        />
        <Route
          path='orderdetail'
          element={
            <>
              <PageTitle breadcrumbs={widgetsBreadCrumbs}>Charts</PageTitle>
              <OrderDetail />
            </>
          }
        />
        <Route
          path='orderedit'
          element={
            <>
              <PageTitle breadcrumbs={widgetsBreadCrumbs}>Charts</PageTitle>
              <OrderEdit />
            </>
          }
        />
        {/* End Order */}
        
        {/* Start Feed */}
        <Route
          path='managerfeed'
          element={
            <>
              <ManagerPost />
            </>
          }
        />
        <Route
          path='feeddetail'
          element={
            <>
              <PageTitle breadcrumbs={widgetsBreadCrumbs}>Lists</PageTitle>
              <FeedDetail />
            </>
          }
        />
        <Route
          path='feededit'
          element={
            <>
              <PageTitle breadcrumbs={widgetsBreadCrumbs}>Lists</PageTitle>
              <FeedEdit />
            </>
          }
        />
        {/* End Feed */}

        {/* Start Rating&Report */}
        <Route
          path='ratingReportDetail'
          element={
            <>
              <PageTitle breadcrumbs={widgetsBreadCrumbs}>Lists</PageTitle>
              <RatingReportDetail />
            </>
          }
        />
        <Route
          path='ratingReportDetailEdit'
          element={
            <>
              <PageTitle breadcrumbs={widgetsBreadCrumbs}>Lists</PageTitle>
              <RatingReportDetail />
            </>
          }
        />
        {/* End Rating&Report */}

        <Route
          path='data-export'
          element={
            <>
              <PageTitle breadcrumbs={widgetsBreadCrumbs}>Mixed</PageTitle>
              <ExportReport />
            </>
          }
        />
        <Route
          path='policy'
          element={
            <>
              <PageTitle breadcrumbs={widgetsBreadCrumbs}>Tables</PageTitle>
              <Policy />
            </>
          }
        />
        <Route
          path='rating-and-report'
          element={
            <>
              <PageTitle breadcrumbs={widgetsBreadCrumbs}>Statiscics</PageTitle>
              <RatingAndReport />
            </>
          }
        />
        <Route
          path='dao'
          element={
            <>
              <Dao />
            </>
          }
        />
        <Route
          path='baocaovipham'
          element={
            <>
              <Baocaovipham />
            </>
          }
        />

        <Route index element={<Navigate to='/profile/partner' />} />
        <Route path='*' element={<Navigate to='/profile/partner' />} />
      </Route>
    </Routes>
  )
}

export default AdminManage
